/*IMPORT */ 
@import './nav';


/* Variable */ 
$hoverColor : rgb(236, 116, 247);


/* Global Code */ 
* {
    box-sizing: border-box;
    margin : 0;
    padding : 0;
}

html {
    scroll-behavior: smooth;
}

body {
    width : 100%;
    overflow-x: hidden;
    font-family : Montserrat;
    background : black;
    overflow-x: hidden;
    
}

a {
    color : inherit;
    text-decoration: none;
}

